@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsans.woff") format("woff"), url("../fonts/ptsans.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsansbold.woff") format("woff"), url("../fonts/ptsansbold.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  font-family: "PT Sans", "Arial", sans-serif;
  background-color: #f2f2f2;
}

body {
  display: grid;
  min-width: 320px;
  max-width: 1200px;
  height: 100%;
  margin: 0 auto;
  background-color: #ffffff;
  grid-template-rows: auto 1fr auto;
}

img {
  max-width: 100%;
  object-fit: cover;
  object-position: center;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.header {
  position: relative;
  min-height: 56px;
}

.main-navigation__logo {
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 1;
  display: block;
  width: 185px;
  height: 96px;
  transform: translateX(-50%);
}

.main-navigation__toggle {
  position: absolute;
  top: 16px;
  right: 20px;
  z-index: 10;
  width: 23px;
  height: 23px;
  background-color: transparent;
  background-image: url("../img/svg/icon-menu.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border: none;
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
  }
}

.main-navigation__list {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1;
  display: none;
  color: #ffffff;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .main-navigation__list {
    position: static;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    background-color: #ffffff;
  }
}

.main-navigation__list::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  height: 7px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .main-navigation__list::after {
    display: none;
  }
}

.main-navigation__item {
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
@media (min-width: 768px) {
  .main-navigation__item {
    width: 153px;
    box-shadow: none;
  }
}
@media (min-width: 1200px) {
  .main-navigation__item {
    width: 239px;
  }
}
@media (min-width: 768px) {
  .main-navigation__item:nth-child(4n-2) {
    margin-right: 76px;
  }
}
@media (min-width: 1200px) {
  .main-navigation__item:nth-child(4n-2) {
    margin-right: 120px;
  }
}
@media (min-width: 768px) {
  .main-navigation__item:nth-child(4n-1) {
    margin-left: 76px;
  }
}
@media (min-width: 1200px) {
  .main-navigation__item:nth-child(4n-1) {
    margin-left: 120px;
  }
}

.main-navigation__link {
  display: block;
  padding: 17px 0 12px 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
}
@media (min-width: 768px) {
  .main-navigation__link {
    font-size: 14px;
    line-height: 21px;
    color: #000000;
  }
  .main-navigation__link:hover, .main-navigation__link:focus {
    color: #81b3d2;
  }
  .main-navigation__link:active {
    color: rgba(0, 0, 0, 0.5);
  }
}

.main-navigation__link--current {
  color: #766357;
}

.main-navigation--open .main-navigation__toggle {
  background-image: url("../img/svg/icon-cross.svg");
}

.main-navigation__toggle:hover,
:focus {
  opacity: 0.5;
}

.main-navigation__toggle:active {
  opacity: 0.3;
}

.main-navigation--open .main-navigation__list {
  display: block;
}

.no-js .main-navigation__toggle {
  display: none;
}

.no-js .main-navigation__list {
  position: relative;
  display: block;
}
@media (min-width: 768px) {
  .no-js .main-navigation__list {
    position: static;
  }
}

