  text-decoration: none;
  background-color: #81b3d2;
  border: none;
}
.button:hover, .button:focus {
  background-color: #669ec0;
  outline: none;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
.button:disabled {
  background-color: #e5e5e5;
}

.map iframe {
  display: block;
  width: 100%;
  height: 350px;
  border: none;
}

.map__image {
  display: none;
  width: 100%;
  height: 350px;
  overflow: hidden;
}

.map__image img {
  display: block;
  width: 100%;
  height: 100%;
}

.no-js iframe {
  display: none;
}

.no-js .map__image {
  display: block;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 5;
  display: none;
  justify-content: center;
  align-items: flex-start;
  background-color: rgba(0, 0, 0, 0.3);
}

.modal--open {
  display: flex;
}

.modal__container {
  display: none;
  width: 320px;
  margin: auto;
  background-color: #ffffff;
